.NavItem {
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
    width: auto;
}

.NavItem a {
    color: #FFF;
    text-decoration: none;
    height: 100%;
    padding: 1rem 2rem;
    border-bottom: 4px solid transparent;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active {
    color: #AAA;
    cursor: pointer;
}


